.loadingImg {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.detailPage-ProductInfo {
  display: flex;
  width: 1360px;
  margin: 0 auto;
  box-shadow: 0px 0px 5px #00000030;
  justify-content: space-between;
  padding-top: 100px;

  &-Gallery {
    width: 100%;
    max-width: 33%;
    display: flex;
    position: relative;
    padding-top: 20px;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-bottom: 20px;
    // height: 100%;

    &-image0 {
      display: flex;
      width: 100%;
      // height: 450px;
      margin-bottom: 10px;
      // position: absolute;
      // padding-top: 100%;
      overflow: hidden;

      img {
        // position: absolute;
        // top: 0px;
        // bottom: 0;
        // left: 0;
        // right: 0;
        width: 100%;
        height: 100%;
        box-shadow: 0 0 8px #000000;
      }
    }
    &-image {
      display: flex;
      // width: 15%;
      // position: relative;
      // padding-top: 15%;
      // margin-top: 10px;
      // margin-left: 10px;

      // overflow: hidden;
      // position: absolute;
      flex: 0 0 19%;
      margin-right: 1%;
      height: 86px;
      img {
        // position: absolute;
        // top: 0px;
        // bottom: 0;
        // left: 0;
        // right: 0;
        width: 100%;
        box-shadow: 0 0 2px #000000;
        object-fit: contain;
      }

      &:hover {
        border: 1.5px solid white;
        box-shadow: 0 0 5px white;
      }
    }
  }
  &-BasicInfo {
    width: 100%;
    max-width: 66%;

    &-row {
      &-title {
        display: flex;
        padding: 12px;
        border-bottom: 0.5px solid #00000030;
        border-width: thin;

        p {
          font-size: 16px;
          font-weight: 600;
          padding: 0px 5px;
          margin-block-end: 0;
        }
      }

      &-priceBox {
        display: flex;
        padding: 12px;
        border-bottom: 0.5px solid #00000030;
        border-width: thin;
        flex-direction: column;
        position: relative;

        .priceBox {
          display: flex;
          flex-direction: row;
          align-items: center;

          p {
            margin-block-end: 0;
            padding: 0px 5px;
          }

          #finalPrice {
            font-size: 18px;
            font-weight: 600;
            color: #e5101d;
          }
          #price {
            font-size: 16px;
            color: gray;
            text-decoration: line-through;
          }

          &-Promotion {
            display: flex;
            background-image: linear-gradient(90deg, #b9081d, #f21a4c);
            border-radius: 3px;
            color: #fff;
            font-size: 12px;
            line-height: 1;
            height: 18px;
            align-items: center;
            padding: 0 6px;
          }
        }

        .rating {
          display: flex;

          &::before {
            content: "\2605\2605\2605\2605\2605";
            -webkit-text-fill-color: transparent;
            background-clip: text;
            padding-right: 5px;
            -webkit-background-clip: text;
          }
        }

        .orderCount {
          color: #828282;
          font-size: 14px;
          margin-block-end: 0;
          position: absolute;
          bottom: 6px;
          right: 12px;
          display: flex;
          align-items: center;
          p {
            margin-block-end: 0;
          }
        }
      }

      &-attribute {
        display: flex;
        padding: 12px;
        border-bottom: 0.5px solid #00000030;
        border-width: thin;
        flex-direction: column;
        position: relative;
        .attribute {
          display: flex;
          position: relative;
          margin-top: 15px;

          &-name {
            min-width: 15%;
            font-weight: bold;
            display: flex;

            justify-content: end;
            p {
              margin-block-end: 0;
            }
          }
          &-value {
            display: flex;
            flex-wrap: wrap;
            input {
              display: block;
              text-align: center;
              max-width: 150px;
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
              width: fit-content;
              /* height: 40px; */
              margin-right: 10px;
              margin-top: 5px;
              padding: 0px 10px;
              border: 0.5px solid #00000030;
              transition: 0.25s ease;
              cursor: pointer;

              &:hover {
                background-color: #e5101d;
                color: white;
              }
            }
            #quanityInput {
              width: 50px;
              margin-left: 10px;
            }
            .chosenValue {
              background-color: #e5101d;
              color: white;
            }
          }
        }
      }

      &-addToCart {
        display: flex;
        padding: 12px;
        border-bottom: 0.5px solid #00000030;
        border-width: thin;
        flex-direction: row;
        position: relative;
        justify-content: flex-end;
        align-items: center;

        #statusAddToCart {
          font-size: 0.85em;
          padding-right: 10px;
          font-style: italic;
          color: red;
        }

        #addToCart {
          background-color: darkseagreen;
          color: white;
          border: 0.5px solid #00000030;
          margin-right: 10px;
          font-weight: 500;
          width: fit-content;
          padding: 10px 20px;

          &:hover {
            background-color: seagreen;
            border: 0.5px solid #000000;
          }
        }

        #quickBuy {
          background-color: #d06c72;
          color: white;
          font-weight: 500;
          border: 0.5px solid #00000030;
          width: fit-content;
          padding: 10px 20px;

          &:hover {
            background-color: #e5101d;
            border: 0.5px solid #000000;
          }
        }
      }
      &-benefit {
        display: flex;

        padding: 12px;
        // border-bottom: 0.5px solid #00000030;
        border-width: thin;
        flex-direction: column;
        position: relative;

        .freeReturn {
          &-title {
            display: flex;
            align-content: center;
            flex-direction: row;
            width: 100%;
            background-repeat: no-repeat;
            background-size: 10em;
            font-size: 1em;
            color: white;
            font-weight: 500;

            p {
              margin-block-end: 0;
            }
            img {
              height: 20px;
              padding-left: 5px;
              padding-top: 4px;
            }
          }
        }
      }
    }
  }
}

.detailPage-Description {
  box-sizing: border-box;
  width: 1360px;
  margin: 0 auto;
  margin-top: 20px;
  box-shadow: 0 0 8px #00000030;
  padding: 10px;

  p:nth-child(1) {
    max-height: 600px;
    overflow: scroll;
  }

  img {
    display: none !important;
  }

  br {
    line-height: 5px !important;
    display: block;
    /* margin: 1px 0; */
    content: "";
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    text-align: left !important;
    text-transform: capitalize !important;
    line-height: 1 !important;
    font-weight: lighter !important;

    span {
      font-size: 0.5em !important;
    }
  }
}

.detailPage-ShopInfo {
  display: flex;
  flex-direction: row;
  width: 1360px;
  margin: 0 auto;
  margin-top: 20px;
  box-shadow: 0 0 5px #00000030;

  &-Left {
    width: 100%;
    max-width: 33%;
    display: flex;
    padding: 12px;
    align-items: center;
    border-right: 0.25px solid #00000030;

    &-Image {
      width: 125px;
      position: relative;
      padding-top: 125px;
      display: block;

      img {
        width: 100%;
        position: absolute;
        // border-radius: 50%;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
      }
    }

    &-Info {
      width: 100%;
      max-width: 66%;
      padding-left: 12px;
      text-decoration: none;
      color: black;

      p {
        font-size: 0.95em;
        display: flex;
        justify-content: space-between;
      }

      &-Name {
        font-size: 20px;
        font-weight: bold;
        color: black;
        transition: 0.25s ease;
        &:hover {
          text-decoration: none;
          color: #e5101d;
        }
      }

      .ratingShop {
        display: flex;
        font-size: 18px;
        justify-content: space-between;
        flex-wrap: wrap;
        &::before {
          content: "\2605\2605\2605\2605\2605";
          -webkit-text-fill-color: transparent;
          background-clip: text;
          padding-right: 5px;
          -webkit-background-clip: text;
        }
      }
    }
  }

  &-Right {
    width: 100%;
    max-width: 66%;
    display: flex;
    flex-direction: column;
    padding: 12px;
    justify-content: center;
    &-Row {
      display: flex;
      flex-direction: row;
      &-item {
        display: flex;
        flex-direction: column;
        flex: 0 0 30%;

        align-items: center;

        p {
          font-weight: 500;
          color: black;
          font-size: 18px;
        }

        #shopAge {
          font-weight: 400;
          font-size: 16px;
        }
        #responseTime {
          font-weight: 400;
          font-size: 16px;
        }
        #productAmount {
          font-weight: 400;
          font-size: 16px;
        }
      }
    }
  }
}

/*Responsive*/
@media screen and (max-width: 1360px) {
  .detailPage-ProductInfo {
    width: 95%;
    &-Gallery {
      max-width: 33%;

      &-image {
        height: 6.6vw;
      }
    }
  }
  .detailPage-ShopInfo {
    width: 95%;
  }
  .detailPage-Description {
    width: 95%;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 1000px) {
  .detailPage-ShopInfo {
    &-Left {
      &-Info {
        p {
          font-size: 0.85em;
          display: flex;
          justify-content: space-between;
        }
      }
    }
    &-Right {
      width: 100%;
      max-width: 66%;
      display: flex;
      flex-direction: column;
      padding: 12px;
      /* justify-content: center; */
      align-items: flex-start;
      // justify-content: space-between;

      &-Row {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100%;
        justify-content: space-evenly;
        &-item {
          display: flex;
          flex-direction: row;
          /* flex: 0 0 30%; */
          align-items: flex-end;
          justify-content: space-between;
          width: 100%;
          p {
            font-size: 1em;
          }
          #shopAge {
            font-size: 1em;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 800px) {
  .detailPage-ProductInfo {
    flex-direction: column;
    padding-top: 100px;
    &-Gallery {
      max-width: 100%;
      &-image0 {
        height: 100%;
      }
      &-image {
        height: 19vw;
      }
    }
    &-BasicInfo {
      max-width: 100%;
      &-row {
        &-addToCart {
          justify-content: space-between;
        }
        &-attribute {
          .attribute {
            justify-content: space-between;
            &-value {
              justify-content: flex-end;
            }
          }
        }
      }
    }
  }
  .detailPage-ShopInfo {
    flex-direction: column;
    &-Left {
      max-width: 100%;
      border-right: none;
      p {
        margin-block-end: 0;
        font-size: 1em;
      }
      &-Image {
        width: 20%;
        // height: 5px;
        img {
          border: 1px solid #00000030;
          // height: 15px;
        }
      }
      &-Info {
        width: 80%;
        max-width: none;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        p {
          display: flex;
          justify-content: space-between;
        }
      }
    }
    &-Right {
      max-width: 100%;
      &-Row {
        flex-direction: column;
        &-item {
          flex-direction: row;
          justify-content: space-between;
        }
      }
    }
  }
}
